// import positionVert from './shaders/position.vert.wgsl?raw';
// import colorFrag from './shaders/color.frag.wgsl';
import { vec3, mat4 } from 'gl-matrix';

import { CreateGPUBuffer, CreateGPUBufferUint } from './shaders/shaders';

import { createTransforms, createViewProjection } from '../Util';

import { CubeData, CubeData1 } from './shaders/cube';

const createCamera = require('3d-view-controls');

// const shader = `
// // vertex shader

// struct Uniforms {
//     mvpMatrix : mat4x4<f32>,
// };
// @binding(0) @group(0) var<uniform> uniforms : Uniforms;

// struct Output {
//     @builtin(position) Position : vec4<f32>,
//     @location(0) vColor : vec4<f32>,
// };

// @vertex
// fn vs_main(@location(0) pos: vec4<f32>, @location(1) color: vec4<f32>) -> Output {
//     var output: Output;
//     output.Position = uniforms.mvpMatrix * pos;
//     output.vColor = color;
//     return output;
// }

// // fragment shader

//  @fragment
// fn fs_main(@location(0) vColor: vec4<f32>) -> @location(0) vec4<f32> {
//     return vColor;
// }
// `
const shader = `

struct Uniforms {
    mvpMatrix : mat4x4<f32>,
};
@binding(0) @group(0) var<uniform> uniforms : Uniforms;

struct Output {
    @builtin(position) Position : vec4<f32>,
    @location(0) vColor : vec4<f32>,
};

@vertex
fn vs_main(@location(0) pos: vec4<f32>, @location(1) color: vec4<f32>) -> Output {
    var output: Output;
    output.Position = uniforms.mvpMatrix * pos;
    output.vColor = color;
    return output;
}

// fragment shader

 @fragment
fn fs_main(@location(0) vColor: vec4<f32>) -> @location(0) vec4<f32> {
    return vColor;
}
`



export class Solution {
    divHost: HTMLElement;
    canvas: HTMLCanvasElement;
    ctx: GPUCanvasContext;
    device: GPUDevice;
    format: GPUTextureFormat;
    size: any;
    pipeline: GPURenderPipeline;
    isAnimation: boolean = true;

    camera: Camera;

    uniformBuffer: GPUBuffer
    uniformBindGroup: GPUBindGroup
    textureView: GPUTextureView
    depthTexture: GPUTexture;
    renderPassDescription: GPURenderPassDescriptor

    numberOfVertices: number;
    vertexBuffer: GPUBuffer;
    colorBuffer: GPUBuffer;

    rotation = vec3.fromValues(0, 0, 0);

    // requestAnimationFrame = window.requestAnimationFrame;
    // cancelAnimationFrame = window.cancelAnimationFrame;
    requestAni: any;

    constructor(id: string) {
        this.divHost = document.getElementById(id);

        //     console.log(id, 'webGPU', navigator.gpu);
        this.canvas = document.createElement('canvas');
        this.divHost.appendChild(this.canvas);

        this.initWebGPU();
    }
    async initWebGPU() {
        if (!navigator.gpu) { throw new Error('Not Support WebGPU'); }

        const adapter = await navigator.gpu.requestAdapter({
            powerPreference: 'high-performance'
            // powerPreference: 'low-power'
        })

        if (!adapter) { throw new Error('No Adapter Found'); }

        const device = await adapter.requestDevice()
        this.ctx = this.canvas.getContext('webgpu') as GPUCanvasContext
        const format = navigator.gpu.getPreferredCanvasFormat ? navigator.gpu.getPreferredCanvasFormat() : this.ctx.getPreferredFormat(adapter)
        const devicePixelRatio = window.devicePixelRatio || 1
        this.canvas.width = this.divHost.clientWidth;
        this.canvas.height = this.divHost.clientHeight;
        this.ctx.configure({
            // json specific format when key and value are the same
            device, format,
            // prevent chrome warning
            alphaMode: 'opaque'
        })

        this.size = { width: this.canvas.width, height: this.canvas.height }
        this.device = device;
        this.format = format;

        this.camera = new Camera(this);

        this.initPipeline(this.device, this.format);
    }

    async initPipeline(device: GPUDevice, format: GPUTextureFormat, primitiveType = 'triangle-list') { // triangle-list , triangle-strip
        // create vertex buffers
        // const cubeData = CubeData0();
        // this.numberOfVertices = cubeData.positions.length / 3;
        // this.vertexBuffer = CreateGPUBuffer(device, cubeData.positions);
        // this.colorBuffer = CreateGPUBuffer(device, cubeData.colors);

        const cubeData = CubeData1();
        this.numberOfVertices = cubeData.indexData.length;
        this.vertexBuffer = CreateGPUBuffer(device, cubeData.vertexData);
        this.colorBuffer = CreateGPUBufferUint(device, cubeData.indexData);


        this.pipeline = device.createRenderPipeline({
            layout: 'auto',
            vertex: {
                module: device.createShaderModule({
                    code: shader
                }),
                entryPoint: "vs_main",
                // @ts-ignore
                // buffers: [
                //     {
                //         arrayStride: 12,
                //         attributes: [{
                //             shaderLocation: 0,
                //             format: "float32x3",
                //             offset: 0
                //         }]
                //     },
                //     {
                //         arrayStride: 12,
                //         attributes: [{
                //             shaderLocation: 1,
                //             format: "float32x3",
                //             offset: 0
                //         }]
                //     }
                // ]
                buffers:[
                    {
                        arrayStride: 24,
                        attributes: [
                            {
                                shaderLocation: 0,
                                format: "float32x3",
                                offset: 0
                            },
                            {
                                shaderLocation: 1,
                                format: "float32x3",
                                offset: 12
                            }
                        ]
                    }
                ]
            },
            fragment: {
                module: device.createShaderModule({
                    code: shader
                }),
                entryPoint: "fs_main",
                targets: [
                    {
                        format: this.format as GPUTextureFormat
                    }
                ]
            },
            primitive: {
                topology: "triangle-list",
                // cullMode: 'back'
            },
            depthStencil: {
                format: "depth24plus",
                depthWriteEnabled: true,
                depthCompare: "less"
            }
        });

        // create uniform buffer and layout
        this.uniformBuffer = device.createBuffer({
            size: 64,
            usage: GPUBufferUsage.UNIFORM | GPUBufferUsage.COPY_DST
        });


        this.uniformBindGroup = device.createBindGroup({
            layout: this.pipeline.getBindGroupLayout(0),
            entries: [{
                binding: 0,
                resource: {
                    buffer: this.uniformBuffer,
                    offset: 0,
                    size: 64
                }
            }]
        });

        this.textureView = this.ctx.getCurrentTexture().createView();
        this.depthTexture = device.createTexture({
            size: [this.canvas.width, this.canvas.height, 1],
            format: "depth24plus",
            usage: GPUTextureUsage.RENDER_ATTACHMENT
        });
        this.renderPassDescription = {
            colorAttachments: [{
                view: this.textureView,
                clearValue: { r: 0.0, g: 0.0, b: 0.0, a: 1.0 }, //background color
                //@ts-ignore
                loadValue: { r: 0.0, g: 0.0, b: 0.0, a: 1.0 },
                loadOp: 'clear',
                storeOp: 'store'
            }],
            depthStencilAttachment: {
                view: this.depthTexture.createView(),
                depthClearValue: 1.0,
                depthLoadOp: 'clear',
                depthStoreOp: "store",
                //stencilClearValue: 0,
                //stencilLoadOp: 'clear',
                //stencilStoreOp: "store"
            }
        };

        this.requestAni = requestAnimationFrame(this.loop);
    }
    public loop = () => {
        // console.log(this.rotation);

        if (this.isAnimation) {
            this.rotation[0] += 0.01;
            this.rotation[1] += 0.01;
            this.rotation[2] += 0.01;
        } else {
            this.rotation = [0, 0, 0];
        }

        this.drawing();
        if (this.isAnimation) {
            this.requestAni = requestAnimationFrame(this.loop);
        }

    }
    public drawing() {
        this.camera.update();

        this.device.queue.writeBuffer(this.uniformBuffer, 0, this.camera.mvpMatrix as ArrayBuffer);
        this.textureView = this.ctx.getCurrentTexture().createView();
        this.renderPassDescription.colorAttachments[0].view = this.textureView;
        const commandEncoder = this.device.createCommandEncoder();
        const renderPass = commandEncoder.beginRenderPass(this.renderPassDescription as GPURenderPassDescriptor);

        renderPass.setPipeline(this.pipeline);
        renderPass.setVertexBuffer(0, this.vertexBuffer);
        renderPass.setIndexBuffer(this.colorBuffer, 'uint32');
        // renderPass.setVertexBuffer(1, this.colorBuffer);
        renderPass.setBindGroup(0, this.uniformBindGroup);
        renderPass.drawIndexed(this.numberOfVertices);
        // renderPass.draw(this.numberOfVertices);
        renderPass.end();

        this.device.queue.submit([commandEncoder.finish()]);
    }
    public destroy() {
        this.device.destroy();
        // this.isAnimation = false;
        cancelAnimationFrame(this.requestAni);
        // this.requestAni = undefined;
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}


class Camera {
    public solution: Solution;
    public canvas: HTMLCanvasElement
    public modelMatrix: mat4 // const
    public mvpMatrix: mat4 // const

    public vMatrix: mat4;
    public vpMatrix: mat4;
    public rotation = vec3.fromValues(0, 0, 0);
    public translation = vec3.fromValues(0, 0, 0);
    public camera: any

    public vp: any // const
    constructor(solution: Solution) {
        this.solution = solution;
        this.canvas = solution.canvas;

        this.modelMatrix = mat4.create();
        this.mvpMatrix = mat4.create();
        this.vMatrix = mat4.create();
        this.vpMatrix = mat4.create();
        this.vp = createViewProjection(this.canvas.width / this.canvas.height);
        this.vpMatrix = this.vp.viewProjectionMatrix;

        // add rotation and camera:
        this.camera = createCamera(this.canvas, this.vp.cameraOption);
        // this.camera.mode = 'turntable' // https://github.com/mikolalysenko/3d-view-controls
    }
    public update() {
        const pMatrix = this.vp.projectionMatrix;
        this.vMatrix = this.camera.matrix;
        mat4.multiply(this.vpMatrix, pMatrix, this.vMatrix);

        createTransforms(this.modelMatrix, this.translation, this.rotation);
        mat4.multiply(this.mvpMatrix, this.vpMatrix, this.modelMatrix);
    }
}